import * as XLSX from 'xlsx';

export interface JsonFileData {
	fileName: string;
	data: any;
	error?: string;
}

export const readJsonFiles = async (
	directoryHandle: FileSystemDirectoryHandle,
	filePaths: string[],
	chunkSize?: number,
	progressCallback?: (progress: {
		currentFile: string;
		processedFiles: number;
		totalFiles: number;
		currentChunk: number;
		totalChunks: number;
		filesPerChunk: number;
		chunkProgress: number[];
	}) => void
): Promise<JsonFileData[]> => {
	const jsonFiles: JsonFileData[] = [];
	const SAFE_CHUNK_SIZE = chunkSize || 5; // Use provided chunk size or default to 5
	const chunks: string[][] = [];
	
	// Create chunks of 5 files each
	for (let i = 0; i < filePaths.length; i += SAFE_CHUNK_SIZE) {
		chunks.push(filePaths.slice(i, i + SAFE_CHUNK_SIZE));
	}
	
	console.log(`Reading ${filePaths.length} JSON files in ${chunks.length} safe chunks of ${SAFE_CHUNK_SIZE}...`);
	
	let totalProcessed = 0;
	
	for (let chunkIndex = 0; chunkIndex < chunks.length; chunkIndex++) {
		const chunk = chunks[chunkIndex];
		
		console.log(`Processing chunk ${chunkIndex + 1}/${chunks.length} (${chunk.length} files)`);
		
		for (let fileIndex = 0; fileIndex < chunk.length; fileIndex++) {
			const filePath = chunk[fileIndex];
			
			// Update progress
			if (progressCallback) {
				const chunkProgress = Array.from({ length: chunks.length }, (_, i) => {
					if (i < chunkIndex) return 100; // Completed chunks
					if (i === chunkIndex) {
						const filesInChunk = chunks[i].length;
						const processedInChunk = (fileIndex + 1) / filesInChunk * 100;
						return processedInChunk;
					}
					return 0; // Future chunks
				});
				
				progressCallback({
					currentFile: filePath.split('/').pop() || filePath,
					processedFiles: totalProcessed,
					totalFiles: filePaths.length,
					currentChunk: chunkIndex + 1,
					totalChunks: chunks.length,
					filesPerChunk: SAFE_CHUNK_SIZE,
					chunkProgress
				});
			}
			
			try {
				// Navigate to the file using the path
				const fileHandle = await getFileHandleByPath(directoryHandle, filePath);
				if (fileHandle) {
					const file = await fileHandle.getFile();
					const text = await file.text();
					
					// Parse JSON
					const jsonData = JSON.parse(text);
					
					jsonFiles.push({
						fileName: filePath,
						data: jsonData
					});
				}
			} catch (error) {
				console.error(`Error reading ${filePath}:`, error);
				jsonFiles.push({
					fileName: filePath,
					data: null,
					error: error instanceof Error ? error.message : 'Unknown error'
				});
			}
			
			totalProcessed++;
		}
		
		// Small delay between chunks to allow UI updates
		await new Promise(resolve => setTimeout(resolve, 100));
	}
	
	console.log(`Successfully read ${jsonFiles.length} files`);
	return jsonFiles;
};

// Helper function to navigate to a file using its path
async function getFileHandleByPath(
	directoryHandle: FileSystemDirectoryHandle,
	filePath: string
): Promise<FileSystemFileHandle | null> {
	try {
		const pathParts = filePath.split('/');
		let currentHandle = directoryHandle;
		
		// Navigate through directories
		for (let i = 0; i < pathParts.length - 1; i++) {
			currentHandle = await currentHandle.getDirectoryHandle(pathParts[i]);
		}
		
		// Get the file handle
		const fileName = pathParts[pathParts.length - 1];
		return await currentHandle.getFileHandle(fileName);
	} catch (error) {
		console.error(`Error navigating to file ${filePath}:`, error);
		return null;
	}
}


export const generateExcelFromJsonFiles = async (
	jsonFiles: JsonFileData[],
	customFileName?: string,
	chunkSize?: number,
	progressCallback?: (progress: {
		currentFile: string;
		processedFiles: number;
		totalFiles: number;
		currentChunk: number;
		totalChunks: number;
		filesPerChunk: number;
		chunkProgress: number[];
	}) => void
): Promise<void> => {
	// Check file limit to prevent memory issues
	const MAX_FILES = 100;
	if (jsonFiles.length > MAX_FILES) {
		alert(`⚠️ Too many files! Found ${jsonFiles.length} JSON files, but the limit is ${MAX_FILES} files to prevent memory issues.\n\nPlease select a folder with fewer files or split your data into smaller folders.`);
		return;
	}
	
	console.log(`Generating Excel from ${jsonFiles.length} JSON files with safe chunking...`);
	
	try {
		// Monitor memory usage
		const memoryBefore = (performance as any).memory ? (performance as any).memory.usedJSHeapSize : 0;
		console.log(`Memory before processing: ${(memoryBefore / 1024 / 1024).toFixed(2)} MB`);
		
		// Create a new workbook
		const workbook = XLSX.utils.book_new();
	
	// Collect all rows of data
	const allRows: Record<string, any>[] = [];
	const processedFiles: string[] = [];
	const errorFiles: string[] = [];
	
	// Process files in safe chunks to manage memory
	const SAFE_CHUNK_SIZE = chunkSize || 5;
	const chunks: JsonFileData[][] = [];
	for (let i = 0; i < jsonFiles.length; i += SAFE_CHUNK_SIZE) {
		chunks.push(jsonFiles.slice(i, i + SAFE_CHUNK_SIZE));
	}
	
	console.log(`Processing ${jsonFiles.length} files in ${chunks.length} safe chunks of ${SAFE_CHUNK_SIZE}`);
	
	let totalProcessed = 0;
	
	// Process each chunk with progress tracking
	for (let chunkIndex = 0; chunkIndex < chunks.length; chunkIndex++) {
		const chunk = chunks[chunkIndex];
		
		console.log(`Processing chunk ${chunkIndex + 1}/${chunks.length}`);
		
		// Update progress for this chunk
		if (progressCallback) {
			const chunkProgress = Array.from({ length: chunks.length }, (_, i) => {
				if (i < chunkIndex) return 100; // Completed chunks
				if (i === chunkIndex) return 0; // Start of chunk processing
				return 0; // Future chunks
			});
			
			progressCallback({
				currentFile: `Processing chunk ${chunkIndex + 1}`,
				processedFiles: totalProcessed,
				totalFiles: jsonFiles.length,
				currentChunk: chunkIndex + 1,
				totalChunks: chunks.length,
				filesPerChunk: SAFE_CHUNK_SIZE,
				chunkProgress
			});
		}
		
		// Process each file in the chunk
		for (const jsonFile of chunk) {
			if (!jsonFile.error && jsonFile.data) {
				try {
					if (Array.isArray(jsonFile.data)) {
						// If it's an array, each item becomes a row
						jsonFile.data.forEach((item, index) => {
							const flattenedItem = flattenObject(item);
							// Add metadata
							flattenedItem['_source_file'] = jsonFile.fileName;
							flattenedItem['_row_index'] = index;
							allRows.push(flattenedItem);
						});
						processedFiles.push(jsonFile.fileName);
					} else if (typeof jsonFile.data === 'object') {
						// If it's a single object, flatten it and make it one row
						const flattenedItem = flattenObject(jsonFile.data);
						// Add metadata
						flattenedItem['_source_file'] = jsonFile.fileName;
						flattenedItem['_row_index'] = 0;
						allRows.push(flattenedItem);
						processedFiles.push(jsonFile.fileName);
					}
				} catch (error) {
					console.error(`Error processing ${jsonFile.fileName}:`, error);
					errorFiles.push(jsonFile.fileName);
				}
			} else {
				errorFiles.push(jsonFile.fileName);
			}
			
			totalProcessed++;
		}
		
		// Small delay between chunks to allow UI updates
		await new Promise(resolve => setTimeout(resolve, 50));
		
		// Check memory after each chunk
		if ((performance as any).memory) {
			const currentMemory = (performance as any).memory.usedJSHeapSize;
			console.log(`Memory after chunk ${chunkIndex + 1}: ${(currentMemory / 1024 / 1024).toFixed(2)} MB`);
		}
	}
	
	// Create the main data sheet using XLSX's json_to_sheet which handles this perfectly
	const mainSheet = XLSX.utils.json_to_sheet(allRows);
	XLSX.utils.book_append_sheet(workbook, mainSheet, 'Combined Data');
	
	// Create a simple summary sheet
	const summaryData = [
		['Processing Summary'],
		[''],
		['Total Files Processed:', processedFiles.length],
		['Total Records:', allRows.length],
		['Files with Errors:', errorFiles.length],
		[''],
		['Processed Files:'],
		...processedFiles.map(file => [file]),
		[''],
		['Files with Errors:'],
		...errorFiles.map(file => [file])
	];
	
	const summarySheet = XLSX.utils.aoa_to_sheet(summaryData);
	XLSX.utils.book_append_sheet(workbook, summarySheet, 'Summary');
	
	// Generate Excel file and trigger download
	const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
	const blob = new Blob([excelBuffer], { 
		type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' 
	});
	
	// Generate filename
	const baseFileName = customFileName && customFileName.trim() ? customFileName.trim() : 'combined-json-data';
	const dateStr = new Date().toISOString().split('T')[0];
	const fileName = `${baseFileName}-${dateStr}.xlsx`;
	
	const url = URL.createObjectURL(blob);
	const link = document.createElement('a');
	link.href = url;
	link.download = fileName;
	
	document.body.appendChild(link);
	link.click();
	document.body.removeChild(link);
	URL.revokeObjectURL(url);
	
	// Final memory check
	const memoryAfter = (performance as any).memory ? (performance as any).memory.usedJSHeapSize : 0;
	console.log(`Memory after processing: ${(memoryAfter / 1024 / 1024).toFixed(2)} MB`);
	console.log(`Memory used: ${((memoryAfter - memoryBefore) / 1024 / 1024).toFixed(2)} MB`);
	console.log(`Total rows processed: ${allRows.length}`);
	
	} catch (error) {
		console.error('Error generating Excel file:', error);
		alert(`❌ Error generating Excel file: ${error instanceof Error ? error.message : 'Unknown error'}\n\nThis might be due to memory limitations. Try with fewer files or smaller JSON files.`);
	}
};

// Helper function to flatten nested objects
function flattenObject(obj: any, prefix = ''): Record<string, any> {
	const flattened: Record<string, any> = {};
	
	for (const key in obj) {
		if (obj.hasOwnProperty(key)) {
			const newKey = prefix ? `${prefix}.${key}` : key;
			
			if (typeof obj[key] === 'object' && obj[key] !== null && !Array.isArray(obj[key])) {
				Object.assign(flattened, flattenObject(obj[key], newKey));
			} else {
				flattened[newKey] = obj[key];
			}
		}
	}
	
	return flattened;
}
