export interface MemoryTestProgress {
	currentChunk: number;
	totalChunks: number;
	chunkSize: number;
	currentMemory: number;
	peakMemory: number;
	status: string;
}

export interface MemoryTestResult {
	initialMemory: number;
	peakMemory: number;
	memoryUsed: number;
	testDuration: number;
	recommendations: {
		safeMemoryPerChunkMB: number;
		safeTotalMemoryMB: number;
		estimatedFilesPerChunk: number;
		estimatedMaxFiles: number;
		averageFileSizeKB: number;
		warning: string;
	};
	details: {
		browserInfo: string;
		heapLimit: number;
		memoryPressure: 'low' | 'medium' | 'high';
	};
}

export const runMemoryTest = async (
	progressCallback?: (progress: MemoryTestProgress) => void,
	customAverageFileSizeKB?: number
): Promise<MemoryTestResult> => {
	const startTime = Date.now();
	
	// Get initial memory state
	const memoryInfo = (performance as any).memory;
	const initialMemory = memoryInfo ? memoryInfo.usedJSHeapSize : 0;
	const heapLimit = memoryInfo ? memoryInfo.jsHeapSizeLimit : 0;
	
	// Create progressively larger data structures to test memory
	const testData: any[] = [];
	const chunkSizes = [100, 250, 500, 750, 1000, 1500, 2000, 3000, 5000, 7500, 10000];
	let peakMemory = initialMemory;
	
	console.log('🧪 Starting memory test...');
	
	for (let chunkIndex = 0; chunkIndex < chunkSizes.length; chunkIndex++) {
		const chunkSize = chunkSizes[chunkIndex];
		
		// Update progress
		if (progressCallback) {
			const currentMemory = memoryInfo ? memoryInfo.usedJSHeapSize : 0;
			progressCallback({
				currentChunk: chunkIndex + 1,
				totalChunks: chunkSizes.length,
				chunkSize: chunkSize,
				currentMemory: currentMemory,
				peakMemory: peakMemory,
				status: `Testing chunk ${chunkIndex + 1}/${chunkSizes.length} (${chunkSize} items)`
			});
		}
		
		try {
			// Create a chunk of mock JSON-like data
			const chunk = Array.from({ length: chunkSize }, (_, i) => ({
				id: i,
				name: `Test Item ${i}`,
				description: `This is a test description for item ${i} with some additional text to make it larger and more realistic for memory testing purposes`,
				timestamp: new Date().toISOString(),
				nested: {
					value: Math.random() * 1000,
					category: `Category ${i % 10}`,
					metadata: {
						created: Date.now(),
						tags: [`tag${i}`, `category${i % 5}`, `group${i % 3}`],
						extra: {
							data: Array.from({ length: 10 }, (_, j) => `data_${j}`),
							config: { enabled: true, priority: i % 3 }
						}
					}
				}
			}));
			
			testData.push(chunk);
			
			// Check memory after each chunk
			if (memoryInfo) {
				const currentMemory = memoryInfo.usedJSHeapSize;
				peakMemory = Math.max(peakMemory, currentMemory);
				
				console.log(`Chunk ${chunkSize}: Memory = ${(currentMemory / 1024 / 1024).toFixed(2)} MB`);
				
				// If memory usage is getting high, stop the test
				if (currentMemory > heapLimit * 0.8) {
					console.log('⚠️ Stopping test - approaching memory limit');
					if (progressCallback) {
						progressCallback({
							currentChunk: chunkIndex + 1,
							totalChunks: chunkSizes.length,
							chunkSize: chunkSize,
							currentMemory: currentMemory,
							peakMemory: peakMemory,
							status: `Stopped at ${chunkSize} items - approaching memory limit`
						});
					}
					break;
				}
			}
			
			// Longer delay to allow UI updates and garbage collection
			await new Promise(resolve => setTimeout(resolve, 300));
			
		} catch (error) {
			console.log(`❌ Memory test failed at chunk size ${chunkSize}:`, error);
			if (progressCallback) {
				progressCallback({
					currentChunk: chunkIndex + 1,
					totalChunks: chunkSizes.length,
					chunkSize: chunkSize,
					currentMemory: memoryInfo ? memoryInfo.usedJSHeapSize : 0,
					peakMemory: peakMemory,
					status: `Failed at ${chunkSize} items - memory limit reached`
				});
			}
			break;
		}
	}
	
	const endTime = Date.now();
	const testDuration = endTime - startTime;
	
	// Calculate final memory usage
	const finalMemory = memoryInfo ? memoryInfo.usedJSHeapSize : 0;
	const memoryUsed = finalMemory - initialMemory;
	
	// Determine memory pressure
	let memoryPressure: 'low' | 'medium' | 'high' = 'low';
	if (memoryInfo) {
		const memoryPercentage = (finalMemory / heapLimit) * 100;
		if (memoryPercentage > 70) {
			memoryPressure = 'high';
		} else if (memoryPercentage > 40) {
			memoryPressure = 'medium';
		}
	}
	
	// Generate recommendations based on test results
	const recommendations = generateRecommendations(peakMemory, heapLimit, memoryPressure, customAverageFileSizeKB);
	
	// Clean up test data
	testData.length = 0;
	
	// Force garbage collection if available
	if ((window as any).gc) {
		(window as any).gc();
	}
	
	const result: MemoryTestResult = {
		initialMemory,
		peakMemory,
		memoryUsed,
		testDuration,
		recommendations,
		details: {
			browserInfo: getBrowserInfo(),
			heapLimit,
			memoryPressure
		}
	};
	
	console.log('✅ Memory test completed:', result);
	return result;
};

const generateRecommendations = (
	peakMemory: number, 
	heapLimit: number, 
	memoryPressure: 'low' | 'medium' | 'high',
	customAverageFileSizeKB?: number
) => {
	let safeMemoryPerChunkMB: number;
	let safeTotalMemoryMB: number;
	let warning: string;
	
	// Calculate safe memory limits based on heap limit and pressure
	const heapLimitMB = heapLimit / (1024 * 1024);
	
	if (memoryPressure === 'high') {
		// Very conservative limits for high pressure
		safeMemoryPerChunkMB = Math.min(50, heapLimitMB * 0.05); // 5% of heap limit, max 50MB
		safeTotalMemoryMB = Math.min(200, heapLimitMB * 0.15); // 15% of heap limit, max 200MB
		warning = 'High memory pressure detected. Use very conservative memory limits and monitor closely.';
	} else if (memoryPressure === 'medium') {
		// Moderate limits for medium pressure
		safeMemoryPerChunkMB = Math.min(100, heapLimitMB * 0.08); // 8% of heap limit, max 100MB
		safeTotalMemoryMB = Math.min(400, heapLimitMB * 0.25); // 25% of heap limit, max 400MB
		warning = 'Moderate memory pressure. Use conservative memory limits and monitor usage.';
	} else {
		// More generous limits for low pressure
		safeMemoryPerChunkMB = Math.min(200, heapLimitMB * 0.12); // 12% of heap limit, max 200MB
		safeTotalMemoryMB = Math.min(800, heapLimitMB * 0.4); // 40% of heap limit, max 800MB
		warning = 'Low memory pressure. Standard memory limits should work well.';
	}
	
	// Adjust based on actual test results
	const peakMemoryMB = peakMemory / (1024 * 1024);
	if (peakMemoryMB > heapLimitMB * 0.6) {
		// If test used more than 60% of heap, be more conservative
		safeMemoryPerChunkMB *= 0.7;
		safeTotalMemoryMB *= 0.7;
		warning += ' High memory usage detected during test - using conservative limits.';
	}
	
	// Ensure minimum values
	safeMemoryPerChunkMB = Math.max(10, Math.round(safeMemoryPerChunkMB));
	safeTotalMemoryMB = Math.max(50, Math.round(safeTotalMemoryMB));
	
	// Estimate file counts based on typical JSON file sizes
	// Use custom file size or default to 500KB
	const averageFileSizeKB = customAverageFileSizeKB || 500; // Custom or default 500KB average JSON file in memory
	const avgFileSizeMB = averageFileSizeKB / 1024; // Convert to MB
	const estimatedFilesPerChunk = Math.floor(safeMemoryPerChunkMB / avgFileSizeMB);
	const estimatedMaxFiles = Math.floor(safeTotalMemoryMB / avgFileSizeMB);
	
	return {
		safeMemoryPerChunkMB,
		safeTotalMemoryMB,
		estimatedFilesPerChunk,
		estimatedMaxFiles,
		averageFileSizeKB,
		warning
	};
};

const getBrowserInfo = (): string => {
	const userAgent = navigator.userAgent;
	
	if (userAgent.includes('Chrome')) {
		return 'Chrome';
	} else if (userAgent.includes('Firefox')) {
		return 'Firefox';
	} else if (userAgent.includes('Safari')) {
		return 'Safari';
	} else if (userAgent.includes('Edge')) {
		return 'Edge';
	} else {
		return 'Unknown Browser';
	}
};
