import React, { useState } from 'react';
import { FolderData } from '../types/FolderData';
import { downloadExcelFromJsonFiles } from '../utils/fileDownload';
import { MemoryTestResult } from '../utils/memoryTest';

interface FolderResultsProps {
	folderData: FolderData;
	directoryHandle: FileSystemDirectoryHandle | null;
	memoryTestResult?: MemoryTestResult | null;
	averageFileSizeKB?: number;
}

const FolderResults: React.FC<FolderResultsProps> = ({ folderData, directoryHandle, memoryTestResult, averageFileSizeKB }) => {
	const [isGenerating, setIsGenerating] = useState(false);
	const [message, setMessage] = useState<string | null>(null);
	const [progress, setProgress] = useState<{
		currentFile: string;
		processedFiles: number;
		totalFiles: number;
		currentChunk: number;
		totalChunks: number;
		filesPerChunk: number;
		chunkProgress: number[]; // Array showing progress within each chunk
	} | null>(null);
	const [fileName, setFileName] = useState(() => {
		// Default to first JSON file name (without extension) or current date
		if (folderData.files.length > 0) {
			const firstFile = folderData.files[0].split('/').pop() || '';
			return firstFile.replace(/\.json$/i, '') || 'json-data';
		}
		return 'json-data';
	});
	const [chunkSize, setChunkSize] = useState(() => {
		// Prioritize memory test results, then fall back to folder data recommendations
		if (memoryTestResult?.recommendations?.estimatedFilesPerChunk) {
			return memoryTestResult.recommendations.estimatedFilesPerChunk;
		}
		return folderData.estimatedSize?.recommendedChunkSize || 5;
	});

	const handleDownload = async () => {
		if (!directoryHandle) {
			setMessage('Error: Directory handle not available');
			return;
		}

		setIsGenerating(true);
		setMessage(null);
		setProgress(null);

		try {
			const result = await downloadExcelFromJsonFiles(
				folderData, 
				directoryHandle,
				fileName, // Custom filename
				chunkSize, // Custom chunk size
				// Progress callback
				(progressData) => {
					setProgress(progressData);
				}
			);
			setMessage(result.message);
		} catch (error) {
			setMessage('An unexpected error occurred');
		} finally {
			setIsGenerating(false);
			setProgress(null);
		}
	};

	return (
		<div className="folder-results">
			<div className="results-header">
				<h2>JSON Files Found</h2>
				<div className="scan-info">
					Scanned on: {folderData.lastScanned.toLocaleString()}
				</div>
			</div>
			
			<div className="results-content">
				<div className="path-info">
					<strong>Path:</strong> {folderData.path}
				</div>
				
					<div className="count-info">
						<div className="file-count">
							<span className="count-number">{folderData.fileCount}</span>
							<span className="count-label">
								{folderData.fileCount === 1 ? 'JSON file' : 'JSON files'} found
							</span>
							{folderData.fileCount > 100 && (
								<div className="warning-message">
									⚠️ Warning: {folderData.fileCount} files may cause memory issues. Limit is 100 files.
								</div>
							)}
						</div>
						
						{folderData.estimatedSize && (
							<div className="data-size-info">
								<div className="size-stats">
									<div className="size-stat">
										<span className="size-label">Total Size:</span>
										<span className="size-value">{folderData.estimatedSize.totalMB.toFixed(1)} MB</span>
									</div>
									<div className="size-stat">
										<span className="size-label">Avg File Size:</span>
										<span className="size-value">{folderData.estimatedSize.averageFileSizeKB.toFixed(1)} KB</span>
									</div>
								</div>
								{folderData.estimatedSize.memoryWarning && (
									<div className="memory-warning">
										⚠️ {folderData.estimatedSize.memoryWarning}
									</div>
								)}
							</div>
						)}
						
						<div className="chunk-controls">
							<label htmlFor="chunk-size-input" className="chunk-label">
								Processing Chunk Size:
							</label>
							<div className="chunk-input-container">
								<input
									id="chunk-size-input"
									type="number"
									min="1"
									max="20"
									value={chunkSize}
									onChange={(e) => setChunkSize(Math.max(1, Math.min(20, parseInt(e.target.value) || 1)))}
									className="chunk-size-input"
									disabled={isGenerating}
								/>
								<span className="chunk-help">files per chunk</span>
							</div>
								<div className="chunk-recommendation">
									{memoryTestResult?.recommendations ? (
										<>
											<span className="memory-based-rec">
												Memory-based: {memoryTestResult.recommendations.safeMemoryPerChunkMB} MB per chunk
											</span>
											<span className="file-size-assumption">
												(using {averageFileSizeKB || memoryTestResult.recommendations.averageFileSizeKB} KB per file)
											</span>
											<span className="estimated-files">
												~{Math.floor(memoryTestResult.recommendations.safeMemoryPerChunkMB * 1024 / (averageFileSizeKB || memoryTestResult.recommendations.averageFileSizeKB))} files per chunk
											</span>
										</>
									) : (
										<span className="fallback-rec">
											Recommended: {folderData.estimatedSize?.recommendedChunkSize || 5} files per chunk
										</span>
									)}
								</div>
						</div>
						
						<div className="filename-input-container">
							<label htmlFor="filename-input" className="filename-label">
								Excel File Name:
							</label>
							<input
								id="filename-input"
								type="text"
								value={fileName}
								onChange={(e) => setFileName(e.target.value)}
								placeholder="Enter filename..."
								className="filename-input"
								disabled={isGenerating}
							/>
							<span className="filename-extension">.xlsx</span>
						</div>
						
						<button 
							onClick={handleDownload}
							className="download-btn"
							disabled={isGenerating || !directoryHandle || folderData.fileCount > 100 || !fileName.trim()}
							title={folderData.fileCount > 100 ? "Too many files - limit is 100" : "Generate Excel file from JSON files"}
						>
							{isGenerating ? '⏳ Generating...' : '📊 Generate Excel'}
						</button>
						
						{progress && (
							<div className="progress-container">
								<div className="progress-header">
									<span>Processing files...</span>
									<span>{progress.processedFiles}/{progress.totalFiles} files</span>
								</div>
								
								<div className="advanced-progress-bar">
									<div className="progress-bar-container">
										{/* Render chunk segments */}
										{Array.from({ length: progress.totalChunks }, (_, chunkIndex) => {
											const chunkStart = chunkIndex * progress.filesPerChunk;
											const chunkEnd = Math.min(chunkStart + progress.filesPerChunk, progress.totalFiles);
											const chunkWidth = (chunkEnd - chunkStart) / progress.totalFiles * 100;
											
											// Calculate progress within this chunk
											const isCurrentChunk = chunkIndex + 1 === progress.currentChunk;
											const isCompletedChunk = chunkIndex + 1 < progress.currentChunk;
											
											let chunkProgressPercent = 0;
											if (isCompletedChunk) {
												chunkProgressPercent = 100;
											} else if (isCurrentChunk) {
												const filesInCurrentChunk = Math.min(progress.filesPerChunk, progress.totalFiles - chunkStart);
												const processedInChunk = progress.processedFiles - chunkStart;
												chunkProgressPercent = (processedInChunk / filesInCurrentChunk) * 100;
											}
											
											return (
												<div
													key={chunkIndex}
													className={`progress-chunk ${isCurrentChunk ? 'current' : ''} ${isCompletedChunk ? 'completed' : ''}`}
													style={{ width: `${chunkWidth}%` }}
												>
													<div 
														className="progress-chunk-fill"
														style={{ width: `${chunkProgressPercent}%` }}
													></div>
													{/* File lines within chunk */}
													<div className="chunk-file-lines">
														{Array.from({ length: chunkEnd - chunkStart }, (_, fileIndex) => {
															const isProcessed = chunkIndex + 1 < progress.currentChunk || 
																(isCurrentChunk && fileIndex < (progress.processedFiles - chunkStart));
															return (
																<div
																	key={fileIndex}
																	className={`file-line ${isProcessed ? 'processed' : ''}`}
																	title={`File ${chunkStart + fileIndex + 1}`}
																></div>
															);
														})}
													</div>
													{/* Chunk label */}
													<div className="chunk-label">
														{chunkIndex + 1}
													</div>
												</div>
											);
										})}
									</div>
								</div>
								
								<div className="progress-details">
									<div className="chunk-info">
										Chunk {progress.currentChunk}/{progress.totalChunks} 
										<span className="chunk-stats">({progress.filesPerChunk} files per chunk)</span>
									</div>
									<div className="current-file">📄 {progress.currentFile}</div>
								</div>
								
								<div className="progress-legend">
									<div className="legend-item">
										<div className="legend-color completed"></div>
										<span>Completed</span>
									</div>
									<div className="legend-item">
										<div className="legend-color current"></div>
										<span>Current Chunk</span>
									</div>
									<div className="legend-item">
										<div className="legend-color pending"></div>
										<span>Pending</span>
									</div>
								</div>
							</div>
						)}
						
						{message && (
							<div className={`message ${message.includes('Error') ? 'error' : 'success'}`}>
								{message}
							</div>
						)}
					</div>
				
			</div>
		</div>
	);
};

export default FolderResults;
