import React, { useState, useEffect } from 'react';
import { useFolderScanner } from './hooks/useFolderScanner';
import FolderSelector from './components/FolderSelector';
import FolderResults from './components/FolderResults';
import { runMemoryTest, MemoryTestResult, MemoryTestProgress } from './utils/memoryTest';
import './App.css';

const App: React.FC = () => {
	const { folderData, isScanning, error, directoryHandle, scanFolder, resetFolder } = useFolderScanner();
	const [memoryTestResult, setMemoryTestResult] = useState<MemoryTestResult | null>(null);
	const [isTestingMemory, setIsTestingMemory] = useState(false);
	const [memoryTestError, setMemoryTestError] = useState<string | null>(null);
	const [memoryTestProgress, setMemoryTestProgress] = useState<MemoryTestProgress | null>(null);
	const [averageFileSizeKB, setAverageFileSizeKB] = useState(500); // Default 500KB

	// Update slider position when folder data changes
	useEffect(() => {
		if (folderData?.estimatedSize?.averageFileSizeKB) {
			setAverageFileSizeKB(Math.round(folderData.estimatedSize.averageFileSizeKB));
		}
	}, [folderData]);

	const handleMemoryTest = async () => {
		// Store current scroll position to prevent jumping
		const scrollPosition = window.scrollY;
		
		setIsTestingMemory(true);
		setMemoryTestError(null);
		setMemoryTestResult(null);
		setMemoryTestProgress(null);
		
		try {
			const result = await runMemoryTest((progress) => {
				setMemoryTestProgress(progress);
			}, averageFileSizeKB);
			setMemoryTestResult(result);
		} catch (error) {
			setMemoryTestError(error instanceof Error ? error.message : 'Memory test failed');
		} finally {
			setIsTestingMemory(false);
			setMemoryTestProgress(null);
			
			// Restore scroll position to prevent jumping to top
			window.scrollTo(0, scrollPosition);
		}
	};

	return (
		<div className="App">
			<header className="app-header">
				<h1>📊 JSON to Excel Generator</h1>
				<p>Convert JSON files from folders into Excel spreadsheets</p>
			</header>
			
			<main className="app-main">
				<div className="two-column-layout">
					<div className="left-column">
						<FolderSelector 
							onFolderSelect={scanFolder}
							isScanning={isScanning}
						/>
						
						{error && (
							<div className="error-message">
								❌ Error: {error}
							</div>
						)}
						
						<div className="memory-test-section">
							<div className="memory-test-header">
								<h3>🧠 Memory Test</h3>
								<p>Test your browser's memory capacity before processing large datasets</p>
							</div>
							
							<div className="file-size-slider-section">
								<label htmlFor="file-size-slider" className="slider-label">
									Average File Size Assumption:
								</label>
								<div className="slider-container">
									<input
										id="file-size-slider"
										type="range"
										min="50"
										max="2000"
										step="50"
										value={averageFileSizeKB}
										onChange={(e) => setAverageFileSizeKB(parseInt(e.target.value))}
										className="file-size-slider"
										disabled={isTestingMemory}
									/>
									<div className="slider-value">
										{averageFileSizeKB} KB
									</div>
								</div>
								{folderData?.estimatedSize?.averageFileSizeKB && (
									<div className="folder-average-indicator">
										📁 Folder average: {Math.round(folderData.estimatedSize.averageFileSizeKB)} KB
									</div>
								)}
							</div>
							
							<button 
								onClick={handleMemoryTest}
								disabled={isTestingMemory}
								className="memory-test-btn"
							>
								{isTestingMemory ? '⏳ Testing...' : '🔬 Run Memory Test'}
							</button>
							
							{memoryTestError && (
								<div className="memory-test-error">
									❌ {memoryTestError}
								</div>
							)}
							
							{memoryTestProgress && (
								<div className="memory-test-progress">
									<div className="progress-header">
										<span className="progress-status">{memoryTestProgress.status}</span>
									</div>
									
									<div className="progress-bar-container">
										<div className="progress-bar">
											<div 
												className="progress-fill"
												style={{ 
													width: `${(memoryTestProgress.currentChunk / memoryTestProgress.totalChunks) * 100}%` 
												}}
											></div>
										</div>
										<span className="progress-text">
											{memoryTestProgress.currentChunk}/{memoryTestProgress.totalChunks}
										</span>
									</div>
									
									<div className="progress-details">
										<div className="progress-detail">
											<span className="detail-label">Current Memory:</span>
											<span className="detail-value">
												{(memoryTestProgress.currentMemory / 1024 / 1024).toFixed(1)} MB
											</span>
										</div>
										<div className="progress-detail">
											<span className="detail-label">Peak Memory:</span>
											<span className="detail-value">
												{(memoryTestProgress.peakMemory / 1024 / 1024).toFixed(1)} MB
											</span>
										</div>
										<div className="progress-detail">
											<span className="detail-label">Chunk Size:</span>
											<span className="detail-value">{memoryTestProgress.chunkSize.toLocaleString()} items</span>
										</div>
									</div>
								</div>
							)}
							
							{memoryTestResult && (
								<div className="memory-test-results">
									<div className="memory-stats">
										<div className="memory-stat">
											<span className="stat-label">Peak Memory:</span>
											<span className="stat-value">{(memoryTestResult.peakMemory / 1024 / 1024).toFixed(1)} MB</span>
										</div>
										<div className="memory-stat">
											<span className="stat-label">Memory Used:</span>
											<span className="stat-value">{(memoryTestResult.memoryUsed / 1024 / 1024).toFixed(1)} MB</span>
										</div>
										<div className="memory-stat">
											<span className="stat-label">Test Duration:</span>
											<span className="stat-value">{memoryTestResult.testDuration}ms</span>
										</div>
									</div>
									
									<div className="memory-recommendations">
										<h4>📋 Memory-Based Recommendations:</h4>
										<div className="recommendation-item">
											<span className="rec-label">Safe Memory per Chunk:</span>
											<span className="rec-value">{memoryTestResult.recommendations.safeMemoryPerChunkMB} MB</span>
										</div>
										<div className="recommendation-item">
											<span className="rec-label">Safe Total Memory:</span>
											<span className="rec-value">{memoryTestResult.recommendations.safeTotalMemoryMB} MB</span>
										</div>
										<div className="recommendation-item">
											<span className="rec-label">File Size Assumption:</span>
											<span className="rec-value">{memoryTestResult.recommendations.averageFileSizeKB} KB per file</span>
										</div>
										<div className="recommendation-item estimate-item">
											<span className="rec-label">Est. Files per Chunk:</span>
											<span className="rec-value">~{memoryTestResult.recommendations.estimatedFilesPerChunk} files</span>
										</div>
										<div className="recommendation-item estimate-item">
											<span className="rec-label">Est. Max Files:</span>
											<span className="rec-value">~{memoryTestResult.recommendations.estimatedMaxFiles} files</span>
										</div>
										<div className="recommendation-warning">
											⚠️ {memoryTestResult.recommendations.warning}
										</div>
									</div>
									
									<div className="memory-details">
										<div className="detail-item">
											<span className="detail-label">Browser:</span>
											<span className="detail-value">{memoryTestResult.details.browserInfo}</span>
										</div>
										<div className="detail-item">
											<span className="detail-label">Memory Pressure:</span>
											<span className={`detail-value pressure-${memoryTestResult.details.memoryPressure}`}>
												{memoryTestResult.details.memoryPressure.toUpperCase()}
											</span>
										</div>
										<div className="detail-item">
											<span className="detail-label">Heap Limit:</span>
											<span className="detail-value">{(memoryTestResult.details.heapLimit / 1024 / 1024).toFixed(0)} MB</span>
										</div>
									</div>
								</div>
							)}
						</div>
						
						{folderData && (
							<div className="files-display-section">
								<div className="files-display-header">
									<h3>📄 JSON Files Found</h3>
									<div className="files-count">
										{folderData.fileCount} {folderData.fileCount === 1 ? 'file' : 'files'}
									</div>
								</div>
								
								{folderData.files.length > 0 && (
									<div className="files-list">
										<div className="files-container">
											<div className="files-grid">
												{folderData.files.slice(0, 20).map((file, index) => (
													<div key={index} className="file-item">
														<span className="file-icon">📄</span>
														<span className="file-name">{file.split('/').pop()}</span>
													</div>
												))}
											</div>
											{folderData.files.length > 20 && (
												<div className="more-files">
													... and {folderData.files.length - 20} more files
												</div>
											)}
										</div>
									</div>
								)}
							</div>
						)}
					</div>
					
					<div className="right-column">
						{folderData ? (
							<div className="results-section">
								<FolderResults 
									folderData={folderData} 
									directoryHandle={directoryHandle}
									memoryTestResult={memoryTestResult}
									averageFileSizeKB={averageFileSizeKB}
								/>
								<button 
									onClick={resetFolder}
									className="reset-btn"
								>
									Scan Different Folder
								</button>
							</div>
						) : (
							<div className="welcome-section">
								<div className="welcome-card">
									<h3>👋 Welcome!</h3>
									<p>Select a folder containing JSON files using the folder picker on the left to get started.</p>
									<div className="welcome-features">
										<div className="feature-item">
											<span className="feature-icon">📁</span>
											<span>Select folders with JSON files</span>
										</div>
										<div className="feature-item">
											<span className="feature-icon">📊</span>
											<span>Generate Excel spreadsheets</span>
										</div>
										<div className="feature-item">
											<span className="feature-icon">⚡</span>
											<span>Smart chunking for large datasets</span>
										</div>
										<div className="feature-item">
											<span className="feature-icon">🎛️</span>
											<span>Customizable processing options</span>
										</div>
									</div>
								</div>
							</div>
						)}
					</div>
				</div>
			</main>
		</div>
	);
};

export default App;
