import { FolderData } from '../types/FolderData';
import { readJsonFiles, generateExcelFromJsonFiles } from './excelGenerator';

export const downloadExcelFromJsonFiles = async (
	folderData: FolderData, 
	directoryHandle: FileSystemDirectoryHandle,
	customFileName?: string,
	chunkSize?: number,
	progressCallback?: (progress: {
		currentFile: string;
		processedFiles: number;
		totalFiles: number;
		currentChunk: number;
		totalChunks: number;
		filesPerChunk: number;
		chunkProgress: number[];
	}) => void
) => {
	try {
		// Read all JSON files with progress tracking
		console.log(`Starting to read ${folderData.files.length} JSON files...`);
		const jsonFiles = await readJsonFiles(directoryHandle, folderData.files, chunkSize, progressCallback);
		
		console.log(`Successfully read ${jsonFiles.length} files, generating Excel...`);
		
		// Generate Excel file with custom filename and chunk size
		await generateExcelFromJsonFiles(jsonFiles, customFileName, chunkSize, progressCallback);
		
		return { success: true, message: 'Excel file generated successfully!' };
	} catch (error) {
		console.error('Error generating Excel file:', error);
		return { 
			success: false, 
			message: error instanceof Error ? error.message : 'Failed to generate Excel file' 
		};
	}
};
