import { useState, useCallback } from 'react';
import { FolderData } from '../types/FolderData';

export const useFolderScanner = () => {
	const [folderData, setFolderData] = useState<FolderData | null>(null);
	const [isScanning, setIsScanning] = useState(false);
	const [error, setError] = useState<string | null>(null);
	const [directoryHandle, setDirectoryHandle] = useState<FileSystemDirectoryHandle | null>(null);

	const scanFolder = useCallback(async (folderPath: string | FileSystemDirectoryHandle) => {
		setIsScanning(true);
		setError(null);
		
		try {
			let files: string[] = [];
			let actualPath: string = '';

			if (folderPath instanceof FileSystemDirectoryHandle) {
				// Use File System Access API
				actualPath = folderPath.name;
				const scanResult = await scanDirectoryHandle(folderPath);
				files = scanResult.files;
				setDirectoryHandle(folderPath); // Store the directory handle for later use
				
				// Calculate size estimates and recommendations
				const totalMB = scanResult.totalSizeBytes / (1024 * 1024);
				const averageFileSizeKB = files.length > 0 ? (scanResult.totalSizeBytes / files.length) / 1024 : 0;
				
				// Calculate recommended chunk size based on data size
				let recommendedChunkSize: number;
				let memoryWarning = '';
				
				if (totalMB < 5) {
					recommendedChunkSize = 10;
					memoryWarning = '';
				} else if (totalMB < 20) {
					recommendedChunkSize = 5;
					memoryWarning = 'Moderate data size - processing may take longer';
				} else if (totalMB < 50) {
					recommendedChunkSize = 3;
					memoryWarning = 'Large data size - consider splitting into smaller folders';
				} else {
					recommendedChunkSize = 1;
					memoryWarning = 'Very large data size - high memory usage expected';
				}
				
				const newFolderData: FolderData = {
					path: actualPath,
					fileCount: files.length,
					files: files,
					lastScanned: new Date(),
					estimatedSize: {
						totalMB,
						averageFileSizeKB,
						recommendedChunkSize,
						memoryWarning
					}
				};
				
				setFolderData(newFolderData);
			} else {
				// Manual path input - this would need a backend service
				// For now, we'll show an error for manual paths
				throw new Error('Manual path input requires a backend service. Please use the folder picker button.');
			}
		} catch (err) {
			setError(err instanceof Error ? err.message : 'Failed to scan folder');
		} finally {
			setIsScanning(false);
		}
	}, []);

	const resetFolder = useCallback(() => {
		setFolderData(null);
		setError(null);
		setDirectoryHandle(null);
	}, []);

	return {
		folderData,
		isScanning,
		error,
		directoryHandle,
		scanFolder,
		resetFolder
	};
};

// Helper function to recursively scan a directory handle for JSON files and estimate sizes
async function scanDirectoryHandle(
	directoryHandle: FileSystemDirectoryHandle,
	relativePath: string = ''
): Promise<{ files: string[], totalSizeBytes: number, fileSizes: number[] }> {
	const files: string[] = [];
	let totalSizeBytes = 0;
	const fileSizes: number[] = [];
	
	try {
		for await (const [name, handle] of directoryHandle.entries()) {
			const fullPath = relativePath ? `${relativePath}/${name}` : name;
			
			if (handle.kind === 'file') {
				// Only include JSON files
				if (name.toLowerCase().endsWith('.json')) {
					try {
						const file = await (handle as FileSystemFileHandle).getFile();
						const fileSize = file.size;
						files.push(fullPath);
						totalSizeBytes += fileSize;
						fileSizes.push(fileSize);
					} catch (error) {
						console.warn(`Could not get file size for ${fullPath}:`, error);
						// Still add the file but with 0 size
						files.push(fullPath);
						fileSizes.push(0);
					}
				}
			} else if (handle.kind === 'directory') {
				// Recursively scan subdirectories
				const subResult = await scanDirectoryHandle(handle as FileSystemDirectoryHandle, fullPath);
				files.push(...subResult.files);
				totalSizeBytes += subResult.totalSizeBytes;
				fileSizes.push(...subResult.fileSizes);
			}
		}
	} catch (error) {
		console.error(`Error scanning directory ${relativePath}:`, error);
		// Continue scanning other files/directories even if one fails
	}
	
	return { files, totalSizeBytes, fileSizes };
}
